<template>
<!-- https://ip-api.com/ -->
    <div class="chatroom">
        <div class="ad"></div>
        <div class="content">
            <div class="item" v-for="item in list" :key="item">
                <div class="ip-wrap">
                    <div class="ip">{{ item.ipAddress }}</div>
                </div>
                <div class="text-wrap">
                    <div class="text">{{ item.content }}</div>
                </div>
            </div>
        </div>
    </div>
    <div class="sent">
        <div class="match">
            <span>Content :</span>
            <input class="input" v-model="matchInfo" placeholder="text" type="text">
            <button class="post" @click="post">Post</button>
            <button class="refresh" @click="toHome">Refresh</button>
        </div>
    </div>
</template>

<script>
import { db, dbBefore } from '../firebase/init.js'

export default {
    name: 'Home',
    data() {
        return {
            ip: null,
            matchInfo: null,
            postTime: null,
            postTimeSecond: null,
            list: [],
        }
    },
    methods: {
        post() {
            const hour = new Date().getHours(Date.now())
            const minute = new Date().getMinutes(Date.now())
            const second = new Date().getSeconds(Date.now())
            this.postTime = `${hour}:${minute}:${second}`
            this.postTimeSecond = hour * 3600 + minute * 60 + second
            db.collection('person').doc(this.ip).set({
                ipAddress: this.ip,
                content: this.matchInfo,
                created: this.postTimeSecond,
            })
            db.collection('person')
                .orderBy('created', 'desc')
                .get()
                .then((querySnapshot) => {
                    this.list = []
                    querySnapshot.forEach((queryDocumentSnapshot) => {
                        const pc = queryDocumentSnapshot.data()
                        this.list.push(pc)
                    })
                })
            // fetch('https://worldtimeapi.org/api/ip')
            //     .then((response) => response.json())
            //     .then((timeNow) => {
            //         const hour = new Date().getHours(timeNow.datetime)
            //         const minute = new Date().getMinutes(timeNow.datetime)
            //         const second = new Date().getSeconds(timeNow.datetime)
            //         this.postTime = `${hour}:${minute}:${second}`
            //         this.postTimeSecond = hour * 3600 + minute * 60 + second
            //         console.log(55555)
            //     })
            //     .then(() => {
            //         db.collection('person').doc(this.ip).set({
            //             ipAddress: this.ip,
            //             content: this.matchInfo,
            //             created: this.postTimeSecond,
            //         })
            //     })
            //     .then(() => {
            //         db.collection('person')
            //             .orderBy('created')
            //             .get()
            //             .then((querySnapshot) => {
            //                 querySnapshot.forEach((queryDocumentSnapshot) => {
            //                     const pc = queryDocumentSnapshot.data()
            //                     this.list.push(pc)
            //                 })
            //             })
            //     })
            // delete data
        },
        toHome() {
            window.location.reload()
        },
    },
    created() {
        fetch('https://api.ipify.org/?format=json')
            .then((response) => response.json())
            .then((data) => {
                this.ip = data.ip
            })
        // delete
        // fetch('https://worldtimeapi.org/api/ip')
        //     .then((response) => response.json())
        //     .then((timeNow) => {
        //         console.log(new Date().getHours(timeNow.datetime),
        //             new Date().getMinutes(timeNow.datetime),
        //             new Date().getSeconds(timeNow.datetime))
        //     })
        db.collection('person')
            .orderBy('created', 'desc')
            .get()
            .then((querySnapshot) => {
                querySnapshot.forEach((queryDocumentSnapshot) => {
                    const pc = queryDocumentSnapshot.data()
                    this.list.push(pc)
                })
            })
        // delete
        dbBefore.collection('state').doc('del').get().then((documentSnapshot) => {
            // 取得上一時段，刪除狀態
            const action = documentSnapshot.get('action')
            // action 處於 false 還沒刪除， 處於 true 代表完成
            if (!action) {
                // 處於未完成
                const targetDel = dbBefore.collection('person')
                dbBefore.collection('person').get().then((querySnapshot) => {
                    console.log(querySnapshot)
                    querySnapshot.forEach((queryDocumentSnapshot) => {
                        targetDel.doc(queryDocumentSnapshot.id).delete()
                    })
                })
                db.collection('state').doc('del').set({ action: false })
                dbBefore.collection('state').doc('del').set({ action: true })
            }
        })
    },
}
</script>

<style lang="scss" scoped>
* {
    border-style: none;
    box-sizing: border-box;
}
.chatroom {
    height: 500px;
    overflow: hidden;
    padding: 10px;
    margin-bottom: 30px;

    display: flex;

    // border: 1px solid blue;
    .ad {
        flex: 0 1 20%;
    }
    .content {
        // border: 1px solid green;
        flex: 1 1 auto;
    }
}

.item {
    display: flex;
    padding: 5px;
    margin-bottom: 10px;

    border-bottom: 1px solid rgb(109, 107, 107);
    // justify-content: center;
    .ip-wrap {
        border: 1px solid red;

        padding: 3px 0;
        margin-right: 25px;
        flex: 0 1 130px;

        display: flex;
        justify-content: center;
        .ip {
            color:#B3C1C3;
        }
    }
    .text-wrap {
        display: flex;
        align-items: center;
        .text {
            color:#B3C1C3;
        }
    }
}

.sent {
    padding-left: 21%;
    .match {
        display: flex;
        align-items: center;
        span {
            color: #B3C1C3;
            font-size: 20px;

            margin-right: 15px;
        }
        .input {
            width: 500px;
            height: 15px;
            margin-right: 20px;

            font-size: 20px;
            color: #B3C1C3;

            background: #1C1B22;
            border: 1px solid #1C1B22;
            border-radius: 8px;
            padding: 15px;
            outline: none;

            transition: all 0.2s;
            &:focus {
                box-shadow: 0 0 1px 1px rgb(197, 197, 197);
            }
        }

        .post {
            width: 60px;
            height: 30px;

            margin-right: 15px;

            background-color: #332872;
            // border: 1px solid #1C1B22;
            border-radius: 5px;
            color: #B3C1C3;
            transition: .1s;
            cursor: pointer;
            font-size: 15.5px;
            &:hover {
                transform: scale(1.09);
            }
            &:active{
                background:olive;
            }
        }
        .refresh {
            padding: 8px 10px;

            margin-right: 15px;

            background-color: #0a262b;
            // border: 1px solid #1C1B22;
            border-radius: 5px;
            color: #B3C1C3;
            transition: .1s;
            cursor: pointer;
            font-size: 15.5px;
            &:hover {
                transform: scale(1.09);
            }
        }
    }
}
</style>
